import {  Autocomplete, Box,  Button,  CircularProgress, FormControlLabel, Stack, Switch, TextField, Typography } from '@mui/material';
import DetailCard from '../components/DetailCard';
import { useEffect, useState } from 'react';
import axios from 'axios';

const pathogenList = [
  "Paramuricea clavata",
"Bactrocera latifrons",
"Drosophila obscura",
"Drosophila kikkawai",
"Aceria tosichella",
"Aphis craccivora",
"Bactrocera dorsalis",
"Brugia malayi",
]
const NoDataAvailable = () => {
  return (
    <Stack justifyContent='center' alignItems='center' sx={{width: '100%', gridColumn: '1/-1',  height: 'calc(100vh - 98px)', backgroundColor: '#063c80'}}>
      <Stack flexDirection='column' justifyContent='center' alignItems='center'>
      <img style={{width: '50%'}} src="images/NoDataAvailable.gif" alt="No Data Available" />
      <Typography color='#fff' sx={{fontSize: {xs:'1.5rem', sm:'3rem'} }} component='p'>No Data Found</Typography>
    </Stack>
    </Stack>
  )
}

const Details = () => {

  const [fetchedData, setFetchedData] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [inputSearch, setInputSearch] = useState('')
  const [dropdownSearch, setDropdownSearch] = useState('')
  const [value, setValue] = useState(null);
  const [infected, setInfected] = useState(false)

  const [filteredList, setFilteredList] = useState([])

  useEffect(()=>{
    axios
			.get("/api/details")
			.then(res => {
        setFetchedData([...res.data])
        setFilteredList([...res.data])
        setIsLoading(false)
      }
        )
			.catch(err => console.log(err));
  },[])

  const onSearch = () => {
    setFilteredList(fetchedData.filter(item => (item.name.toLowerCase().includes(inputSearch.toLowerCase())) &&  (item.pathogensData.some(pathogen => (pathogen.pathogensName.includes(dropdownSearch)))) && (item.pathogensData.some(pathogen => infected?(pathogen.pathogensName.includes(dropdownSearch) && pathogen.infected === infected): (pathogen.pathogensName.includes(dropdownSearch))))
    ))
  }

  const onReset = () => {
    setFilteredList(fetchedData)
    setInputSearch('')
    setValue(null)
    setInfected(false)

  }
  

    if (isLoading) {
    return <Box sx={{minHeight: 'calc(100vh - 64px)'}}>
      <Stack justifyContent='center' alignItems='center' sx={{minHeight: 'calc(100vh - 64px)'}}>
      <CircularProgress size={200}/>
      </Stack>
    </Box>
    }




  return (
    <>
    <Box sx={{minHeight: 'calc(100vh - 64px)', backgroundColor: '#b4d5ff', pt: '1rem'}}>
      <Box sx={{ marginInline: "auto", display: "flex", backgroundColor: "#fff", width: "75%", padding: ".5rem", borderRadius: "5px", gap: '1rem', justifyContent: 'center'}}>
        <TextField type='search' placeholder='Search' sx={{flexGrow: '1', input: {height: '39px'}}} value={inputSearch} onChange={(e)=> setInputSearch(e.target.value)} variant='standard'  />
        <Autocomplete
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        inputValue={dropdownSearch}
        onInputChange={(event, newInputValue) => {
          setDropdownSearch(newInputValue);
        }}
        id="controllable-states-demo"
        options={pathogenList}
        sx={{ width: 300 }}
        renderInput={(params) => <TextField {...params} label="Pathogens" variant='standard' />}
      />
      <FormControlLabel sx={{width: 'fit-content'}} color="secondary" control={<Switch checked={infected} onChange={(e) => setInfected(e.target.checked)} />} label="Infection Status" />
      <Button onClick={onSearch}>Search</Button>
      <Button onClick={onReset}>Reset</Button>
      </Box>
    <Stack flexDirection='row' flexWrap="wrap" sx={{ height: '100%', p: '1rem', gap: {xs: '.5rem', md: '1rem'}}}>
        {filteredList.length === 0? <NoDataAvailable /> : filteredList.map((item, indx) => <DetailCard key={indx} {...item} />)}
    </Stack>
    </Box>
    </>
  )
}

export default Details