import { Box, Stack, Typography } from "@mui/material";

const DetailCard = ({ _id, name, pathogensData }) => {
	if (pathogensData.length === 0) {
		return <h2 style={{ color: "#27496D" }}>please wait...</h2>;
	}

	return (
		<Stack
			flexDirection="row"
			sx={{
				width: "100%",
				padding: ".5rem",
				borderRadius: "15px",
				boxShadow: "0px 2px 9px 1px #004e64",
				textShadow: "0 0 #004e64",
				backgroundColor: "#fefefe",
			}}>
			<Stack
				flexDirection="column"
				sx={{ py: "0.5rem", px: "1rem", width: "fit-content" }}>
				<Stack
					gap={1}
					direction={{ xs: "column", md: "row" }}
					justifyContent="space-between"
					sx={{ pb: "0.6rem" }}>
					<Typography variant="body1" sx={{ color: "#7400b8" }}>
						ID:{" "}
						<span style={{ color: "#004e64", fontWeight: "bold" }}>
							{String(_id).slice(-3)}
						</span>
					</Typography>
					<Typography
						variant="body1"
						sx={{ textTransform: "capitalize", color: "#7400b8" }}>
						Name:{" "}
						<span style={{ color: "#004e64", fontWeight: "bold" }}>{name}</span>{" "}
					</Typography>
				</Stack>
				<Box
					sx={{
						display: "grid",
						gridTemplateColumns: "1fr 1fr 1fr 1fr",
						gap: "1rem",
					}}>
					{pathogensData.map((pathogen, indx) => (
						<Stack flexDirection="row" sx={{ width: "fit-content" }} key={indx}>
							<Box
								sx={{
									height: "80px",
									width: "80px",
									borderRadius: "10px",
									overflow: "hidden",
								}}>
								<img
									style={{ width: "80px", height: "80px" }}
									src={pathogen.image || 'images/NoDataAvailable1.png'}
									alt="pathogen"
								/>
							</Box>
							<Stack
								flexDirection="column"
								justifyContent="center"
								gap={1}
								ml={2}>
								<Typography
									variant="body1"
									sx={{ color: "#7400b8", fontSize: ".8rem" }}>
									Pathogen's Name:{" "}
									<span style={{ color: "#004e64", fontWeight: "bold" }}>
										{pathogen.pathogensName}
									</span>
								</Typography>
								<Typography
									variant="body1"
									sx={{ color: "#7400b8", fontSize: ".8rem" }}>
									Status:
									{pathogen.infected ? (
										<span style={{ color: "darkred", marginLeft: ".3rem" }}>
											Infected
										</span>
									) : (
										<span style={{ color: "darkgreen", marginLeft: ".3rem" }}>
											Not Infected
										</span>
									)}{" "}
								</Typography>
							</Stack>
						</Stack>
					))}
				</Box>
			</Stack>
		</Stack>
	);
};

export default DetailCard;
